.page-footer-contacts h2 {
  margin: 0;
  margin-bottom: 26px;
  padding-left: 2.1%;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1b2d37;
}

.page-footer-contacts__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  padding-left: 2.1%;
  height: 70%;
  list-style: none;
}

.page-footer-contacts__item {
  margin-bottom: 0;
}

.page-footer-contacts__item {
  &:first-child {
    margin-bottom: 3px;
    opacity: 0.5;
    letter-spacing: -0.6px;
  }
}

.page-footer-contacts__link {
  font-size: 15px;
  line-height: 25px;
  color: #1b2d37;
  opacity: 0.5;

  &:hover {
    color: #00b2ff;
    opacity: 1;
  }
}

@media (max-width: $tablet-width) {
  .page-footer-contacts {
    padding-left: 8px;
  }
}
