.main-nav {
  display: flex;
  margin: 0 auto;
  margin-bottom: 225px;
  padding-top: 33px;
  max-width: 1422px;
}

.main-nav__logo {
  display: flex;
  margin-right: auto;
  padding-left: 13px;
  min-width: 230px;
}

.main-nav img {
  height: fit-content;
}

.main-nav__logo div {
  padding-top: 6px;
  padding-left: 16px;
}

.main-nav__logo p {
  margin: 0;
  line-height: 0;
}

.main-nav__logo-title {
  font-family: $second-font;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff;
}

.main-nav__logo-subtitle {
  font-family: $third-font;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  padding-top: 15px;
  list-style-type: none;
}

.main-nav__item {
  margin-right: 70px;
  margin-bottom: 20px;

  &:nth-child(2n) {
    margin-right: 25px;
  }

  &:last-child {
    margin-right: 0;
  }
}

.main-nav__link {
  position: relative;
  color: #ffffff;
  padding-top: 14px;
  padding-right: 26px;
  padding-bottom: 14px;
  padding-left: 25px;

  &--callback {
    font-family: $second-font;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border: 1px solid #ffffff;
    border-radius: 5px;
  }

  &:hover {
    color: #00b2ff;
    border-color:#00b2ff;

    .main-nav__icon {
      transform: scale(1.3);
    }
  }
}

.main-nav__icon {
  position: absolute;
  top: 16px;
  left: -13px;
  fill: none;
  stroke: #00b2ff;

  &--phone {
    left: -7px;
  }
}

@media (max-width: $tablet-width) {
  .main-nav {
    margin-bottom: 116px;
    padding-top: 20px;
  }
  
  .main-nav__logo {
    padding-left: 0;
  }

  .main-nav__list {
    padding-top: 10px;
  }

  .main-nav__item {
    margin-right: 0;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  .main-nav__link-blink,
  .main-nav__link--callback {
    display: none;
  }

  .main-nav__link {
    padding-right: 20px;
    padding-left: 20px;
    top: 2px;
    left: -9px;

    &--phone {
      left: 10px;
    }
  }

  .main-nav__icon {
    left: 0;
    width: 25px;
    height: 20px;
    top: 14px;
    left: 8px;
  
    &--phone {
      width: 24px;
      height: 24px;
      top: 14px;
      left: 8px;
    }
  }
}

@media (max-width: $mobile-width) {
  .main-nav__logo {
    min-width: fit-content;
  }

  .main-nav__logo div {
    display: none;
  }
}
